body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

body {
  background-color: rgb(246, 246, 246);
}
.main-container {
  padding-top: 90px;
  padding-bottom: 15px;
  max-width: 1200px;
  margin: 0 auto;
}
.plugins-page-container {
  text-align: center; }
  .plugins-page-container header {
    margin-bottom: 60px; }
    .plugins-page-container header h2 {
      color: #883996;
      text-transform: uppercase;
      font-size: 2rem;
      margin: 0; }
    .plugins-page-container header p {
      font-size: 1.25rem;
      font-style: italic;
      font-family: sans-serif;
      margin: 0; }
    .plugins-page-container header hr {
      width: 100px;
      border: none;
      margin-top: 30px;
      border-top: 1px solid #883996; }
  .plugins-page-container .plugin-container {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around; }

.whiteframe {
  box-shadow: 0 4px 15px 0 rgba(0, 0, 0, 0.1); }

.toolbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 60px;
  background-color: #ffffff;
  z-index: 1;
  -webkit-transition: height 0.2s;
  transition: height 0.2s; }

.toolbar-content {
  position: relative;
  display: flex;
  flex: 1 1 auto;
  flex-direction: row;
  align-items: center;
  height: 100%;
  margin: 0 15px; }

.toolbar-brand-container {
  display: flex;
  flex: 1 1 auto;
  align-items: center;
  padding: 10px 0; }

.toolbar-brand {
  display: inherit; }
  .toolbar-brand a {
    text-decoration: none; }

.toolbar-brand img {
  height: 35px;
  -webkit-transition: all 0.2s;
  transition: all 0.2s; }

.toolbar-option-container {
  display: none;
  flex: 1 1 auto;
  flex-direction: row;
  height: 100%;
  justify-content: flex-end; }

@media (min-width: 768px) {
  .toolbar-option-container {
    display: flex; } }

.toolbar-option {
  display: flex;
  flex: 1 1 auto;
  max-width: 120px;
  min-width: 60px;
  justify-content: center;
  align-items: center;
  padding: 5px;
  text-transform: uppercase;
  cursor: pointer;
  font-size: .9em; }
  .toolbar-option span {
    margin-left: 10px; }

.toolbar-option:hover {
  background-color: #f6f6f6; }

a.toolbar-option {
  color: #444;
  text-decoration: none; }

a.toolbar-option.active {
  color: #883996; }

.toolbar-option > img {
  width: 1em;
  height: 1em; }

.mobile-menu-button {
  display: block; }

@media (min-width: 768px) {
  .mobile-menu-button {
    display: none; } }

.mobile-menu-button .icon-bar {
  display: block;
  height: 3px;
  width: 19px;
  margin-top: 3px;
  background-color: #444444;
  border-radius: 1px; }

.mobile-menu {
  position: fixed;
  top: 60px;
  left: 0;
  right: 0;
  max-height: calc(100% - 60px);
  overflow-y: auto;
  display: none;
  background-color: #ffffff;
  z-index: 1; }

.mobile-menu .menu-container {
  display: block;
  position: relative;
  margin: 0;
  padding: 0; }

.mobile-menu .menu-container > .menu-item {
  display: block;
  font-weight: bold;
  padding: 0 50px;
  min-height: 50px;
  line-height: 50px;
  background-color: #f6f6f6;
  border-top: 1px solid #dfe5e9;
  cursor: pointer; }

.mobile-menu .menu-container > .menu-item a {
  text-decoration: none;
  color: #444444;
  width: 100%;
  margin: 0 -50px;
  padding: 0 50px; }

.mobile-menu .menu-container > .menu-item > .menu-sub-item-container {
  display: none;
  margin: 0 -50px; }

.mobile-menu .menu-container > .menu-item > .menu-sub-item-container .menu-item {
  font-weight: normal;
  background-color: #ffffff; }

.navigation {
  display: flex;
  justify-content: center; }
  .navigation .numero-de-page {
    margin: 40px 20px; }
    .navigation .numero-de-page button {
      margin: 0 3px;
      color: #FFF;
      background-color: #883996;
      border: 1px solid #883996;
      border-radius: 5px; }
      .navigation .numero-de-page button:hover {
        color: #883996;
        background-color: #FFF;
        cursor: pointer; }
    .navigation .numero-de-page .current-page {
      font-weight: bold;
      font-size: 1.5em; }
  .navigation .page-precedente, .navigation .page-suivante {
    display: flex;
    align-items: center; }
    .navigation .page-precedente:hover, .navigation .page-suivante:hover {
      color: #883996;
      cursor: pointer; }
    .navigation .page-precedente p, .navigation .page-suivante p {
      margin: 0 10px; }
    .navigation .page-precedente svg, .navigation .page-suivante svg {
      font-size: 2em; }

.pedalboard {
  background-color: #FFF;
  max-width: 770px;
