  padding: 15px;
  box-shadow: 0 4px 15px 0 rgba(0, 0, 0, 0.1); }
  .pedalboard header {
    display: flex;
    justify-content: space-between; }
    .pedalboard header .author {
      display: flex;
      flex-flow: column nowrap; }
      .pedalboard header .author .author-name {
        font-weight: bold;
