      align-items: center; }
      .pedalboard header .count-container .count {
        display: inline-block;
        height: 40px;
        width: 40px;
        text-align: center;
        line-height: 40px;
        margin: 3px;
        background-color: #883996;
        text-decoration: none;
        color: #ffffff;
        font-weight: bold;
        font-size: 1.5em; }
        .pedalboard header .count-container .count span {
          text-align: center;
          line-height: 40px;
          color: #ffffff;
          font-weight: bold;
          font-size: 1.5em; }
  .pedalboard > h2 {
    font-weight: bold;
    font-size: 1.5em;
    color: #883996; }
  .pedalboard .plugin-media-container {
    max-width: 100%;
    max-height: 400px;
    text-align: center; }
    .pedalboard .plugin-media-container img {
      width: auto;
      height: auto;
      max-height: 400px; }
  .pedalboard .category-container {
    display: flex;
    justify-content: center; }
    .pedalboard .category-container .plugin-category {
      padding: 5px;
      background-color: #883996;
      color: #ffffff;
      text-decoration: none;
      font-size: .8em;
      display: inline-block;
      margin: 5px;
      border-radius: 5px; }
  .pedalboard .pedalboard-description {
    white-space: pre-wrap;
