        text-align: left; }
  .pedalboard .plugin-misc-info-container {
    display: flex;
    justify-content: space-between; }

.plugin {
  width: 200px;
  background-color: #FFF;
  padding: 10px;
  margin: 10px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  box-shadow: 0 4px 15px 0 rgba(0, 0, 0, 0.1); }
  .plugin img {
